import './App.css';
import React from 'react';
import { useRef, useLayoutEffect, useState } from 'react';

// GSAP imports
import gsap from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger'

// Font imports
import "./fonts/Satoshi-Bold.ttf";
import "./fonts/Satoshi-Regular.ttf";

// Image imports
import arrowLight from "./images/arrowLight.svg";
import arrowMedium from "./images/arrowMedium.svg";
import arrowDark from "./images/arrowDark.svg";
import allerguideLogin from "./images/allerguideLogin2.png";
import allerguideAllergenSelect from "./images/allerguideAllergenSelect2.png";
import allerguideRest from "./images/allerguideRest.png";
import cubecrusher1 from "./images/cubecrusher1.png";
import cubecrusher2 from "./images/cubecrusher2.png";
import addmoneymod1 from "./images/addmoneymod1.png";
import addmoneymod2 from "./images/addmoneymod2.png";
import leaguedrafter2 from "./images/leaguedrafter2.png";
import githubMark from "./images/github-mark.svg";
import linkedinMark from "./images/iconmonstr-linkedin-3.png";

// MUI imports
// npm install @mui/joy @emotion/react @emotion/styled
import Tabs from '@mui/joy/Tabs';
import TabList from '@mui/joy/TabList';
import Tab, { tabClasses } from '@mui/joy/Tab';
import TabPanel from '@mui/joy/TabPanel'

// when committing changes
// npm run deploy -- -m "Comment here"

gsap.registerPlugin(ScrollTrigger)

function App() {

  // Creating animations and timeline using GSAP

  //let masterTimeline = new gsap.timeline();

  const appRef = useRef(null);
  const adamRef = useRef(null);
  const introRef = useRef(null);
  const projRef = useRef(null);
  
  const arrowLightRef = useRef(null);
  const arrowMediumRef = useRef(null);
  const arrowDarkRef = useRef(null);

  function screen1Animation() {
    let tl = new gsap.timeline();

    const el1 = adamRef.current;
    const el2 = introRef.current;
    const el3 = projRef.current;

    tl.fromTo(el1, {y: -200}, {y: 0, duration: 2, ease: "bounce"});
    tl.fromTo(el2, {opacity: 0}, {opacity: 1, duration: 1});
    tl.fromTo(el3, {opacity: 0}, {opacity: 1, duration: 2});
  }

  function arrowsAnimation()  {
    let tl = new gsap.timeline({repeat: -1});

    const el1 = arrowLightRef.current;
    const el2 = arrowMediumRef.current;
    const el3 = arrowDarkRef.current;

    tl.fromTo(el1, {opacity: 0}, {opacity:1, duration: 0.25});
    tl.fromTo(el2, {opacity: 0}, {opacity:1, duration: 0.25});
    tl.fromTo(el3, {opacity: 0}, {opacity:1, duration: 0.25});

    tl.fromTo(el1, {opacity: 1}, {opacity:0, duration: 0.25});
    tl.fromTo(el2, {opacity: 1}, {opacity:0, duration: 0.25});
    tl.fromTo(el3, {opacity: 1}, {opacity:0, duration: 0.25});
  }

  // For each subsequent screen swap the background color to match the card in the middle

  function screen2Swap() {
    let tl = new gsap.timeline({
      scrollTrigger: {
        trigger: ".Screen2",
        start: "top top+=300",
        end: "+=0",
        scrub: 1.5,
      }
    });

    const el1 = appRef.current;

    tl.fromTo(el1, {backgroundColor: 'white'}, {duration: 1, backgroundColor: '#5ac7f5', ease: "linear"});
  }

  function screen3Swap() {
    let tl = new gsap.timeline({
      scrollTrigger: {
        trigger: ".Screen3",
        start: "top top+=300",
        end: "+=0",
        scrub: 1.5,
      }
    });

    const el1 = appRef.current;

    tl.fromTo(el1, {}, {duration: 1, backgroundColor: '#011118', ease: "linear"});
  }

  function screen4Swap() {
    let tl = new gsap.timeline({
      scrollTrigger: {
        trigger: ".Screen4",
        start: "top top+=300",
        end: "+=0",
        scrub: 1.5,
      }
    });

    const el1 = appRef.current;

    tl.fromTo(el1, {}, {duration: 1, backgroundColor: '#015F87', ease: "linear"});
  }

  function screen5Swap() {
    let tl = new gsap.timeline({
      scrollTrigger: {
        trigger: ".Screen5",
        start: "top top+=300",
        end: "+=0",
        scrub: 1.5,
      }
    });

    const el1 = appRef.current;

    tl.fromTo(el1, {}, {duration: 1, backgroundColor: '#011118', ease: "linear"});
  }

  function screen6Swap() {
    let tl = new gsap.timeline({
      scrollTrigger: {
        trigger: ".Screen6",
        start: "top top+=300",
        end: "+=0",
        scrub: 1.5,
      }
    });

    const el1 = appRef.current;

    tl.fromTo(el1, {}, {duration: 1, backgroundColor: 'white', ease: "linear"});
  }

  // Register timeline to start animations on page use
  useLayoutEffect(() => {
    let masterTimeline = new gsap.timeline();

    let ctx = gsap.context(() => {
      masterTimeline.add(screen1Animation()).add(arrowsAnimation(), ">+=4").add(screen2Swap()).add(screen3Swap()).add(screen4Swap()).add(screen5Swap()).add(screen6Swap());
    });

    return () => ctx.revert();
  }, [])

  // State variable for selecting active tab on last page
  const [tabValue, setTabValue] = useState(0);
  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  }

  // Variables for use in hyperlinks
  var thunderstoreLink = <a href="https://thunderstore.io/c/lethal-company/p/Apoly/AddMoneyMod/" rel="noreferrer">Thunderstore</a>
  var githubLink = <a href="https://github.com/Apolywkan/AddMoneyMod" rel="noreferrer">GitHub</a>
  var githubLink2 = <a href="https://github.com/Apolywkan/League-Drafter" rel="noreferrer">GitHub</a>

  return (
    <div className="App" ref={appRef}>

      <div className="Screen1">
        <div className="Name">
          <span className="Hello">Hello, I'm</span>
          <span className="Adam" ref={adamRef}>Adam</span>
        </div>
        <div className="Intro" ref={introRef}>
          A full stack software developer who recently graduated. I have a passion for developing and designing, as well as a strong scientific background I have gained from studying computer science, mathematics, chemistry, and biology across two degrees. 
        </div>
        <div className="ProjectsDiv" ref={projRef}>
          <div className="ArrowContainer">
            <img class="ArrowLight" src={arrowLight} alt="arrowLight" ref={arrowLightRef}></img>
            <img class="ArrowMedium" src={arrowMedium} alt="arrowMedium" ref={arrowMediumRef}></img>
            <img class="ArrowDark" src={arrowDark} alt="arrowDark" ref={arrowDarkRef}></img>
          </div>
          <div className="Projects">Projects</div>
        </div>
      </div>

      <div className="Screen2">
        <div className="AllerGcontainer">
          <div className="S2top">
            <div className="S2desc">
              <div className="S2title1">
                <span className="Aller">Aller</span>
                <span className="Guide">Guide</span>
              </div>
              <div className="S2body1">A webapp designed for providing a fast, easy way to deal with allergy menus. Select your restaraunt and allergens and the app tells you what foods are suitable for your allergies or food sensitivities.</div>
              <div className="S2title2">
                <span className="S2technology">Technology</span>
                <span className="S2implementation">implementation</span>
              </div>
              <div className="S2body2">Front end designed using React, database managed with MySQL, Django used for creating REST API, redux for persistent states, and Google login authenticator to store user accounts and allergy selection.</div>
            </div>
            <div className="S2img1container">
              <img class="AllerguideLogin" src={allerguideLogin} alt="allerguideLogin"></img>
            </div>
          </div>
          <div className="S2bottom">
            <div className="S2img2container">
              <img class="AllerguideRest" src={allerguideRest} alt="allerguideRest"></img>
            </div>
            <div className="S2img3container">
              <img class="AllerguideAllergenSelect" src={allerguideAllergenSelect} alt="allerguideAllergenSelect"></img>
            </div>
          </div>
        </div>
      </div>

      <div className="Screen3">
        <div className="BrickBreakerContainer">
          <div className="S3left">
            <div className="S3title">
              <span className="Brick">Brick</span>
              <span className="Breaker">Breaker</span>
            </div>
            <div className="S3body">A 3D brick breaker game made in WebGL. Ray tracing is implemented using the Blinn-Phong lighting model. All main objects are given their own colliders, detection is done by calculating centroid distance. 
            Physics for the trajectory of the ball off the paddle or blocks is done by calculating the distance of the centroid of the ball to the boundaries of either side of the paddle and influencing the angle of the 
            bounce by the corresponding amount.
            </div>
            <div className="S3img1container">
              <img class="Cubecrusher2" src={cubecrusher2} alt="cubecrusher2"></img>
            </div>
          </div>
          <div className="S3right">
            <div className="S3img2container">
              <img class="Cubecrusher1" src={cubecrusher1} alt="cubecrusher1"></img>
            </div>
          </div>
        </div>
      </div>

      <div className="Screen4">
          <div className="CarRentalContainer">
            <div className="S4inner">
              <div className="S4title">
                <span className="CarRental">AddMoney</span>
                <span className="Website">Mod</span>
              </div>
              <div className="S4body">
                A mod made for the popular game "Lethal Company", which currently has over 20,000 downloads. Written in C# and utilizing BepInEx, this mod allows you to use the in-game terminal to add credits to 
               your balance at any time. The mod and its documentation are available through either {thunderstoreLink} or {githubLink}.
              </div>
              <div className="S4images">
                <div className="S4img1container"><img class="Addmoneymod1" src={addmoneymod1} alt="addmoneymod1"></img></div>
                <div className="S4img2container"><img class="Addmoneymod2" src={addmoneymod2} alt="addmoneymod2"></img></div>
              </div>
            </div>
          </div>
        </div>

        <div className="Screen5">
          <div className="LeagueDrafterContainer">
            <div className="S5title">
              <span className="League">League </span>
              <span className="Drafter">  Drafter</span>
            </div>
            <div className="S5body">A python script that uses machine learning to predict the outcome of a match of the videogame "League of Legends". The tool uses a random forest classifier trained off a data set of completed 
            league of legends games and their outcomes. By providing the script with the champions present in the match and 
            their respective lanes, the algorithm compares the champions winrates and counter-matchups 
            in each lane and predicts whether the match will end in a win or a loss for your team. Documentation available on {githubLink2}.
            </div>
            <div className="S5img1container">
              <img class="Leaguedrafter2" src={leaguedrafter2} alt="leaguedrafter2"></img>
            </div>
          </div>
      </div>

      <div className="Screen6">
        <div className="Screen6Container">
          <div className="S6top">
            <div className="S6left">
              <div className="SkillsContainer">
                <Tabs aria-label="tabs" value={tabValue} onChange={handleTabChange} sx={{ bgcolor: 'transparent' }}>
                  <TabList
                    disableUnderline
                    sx={{
                      p: 0.5,
                      gap: 2,
                      borderRadius: 'xl',
                      bgcolor: '#D9D9D9',
                      [`& .${tabClasses.root}[aria-selected="true"]`]: {
                        boxShadow: 'sm',
                        bgcolor: 'background.surface',
                      },
                    }}
                  >
                    <Tab disableIndicator>Languages</Tab>
                    <Tab disableIndicator>Tools</Tab>
                    <Tab disableIndicator>Skills</Tab>
                    <Tab disableIndicator>Education</Tab>
                  </TabList>
                  <TabPanel value={0}>
                    <div className="panel1">
                      <p>• Python</p>
                      <p>• Javascript</p>
                      <p>• C#</p>
                      <p>• C</p>
                      <p>• SQL</p>
                      <p>• CSS</p>
                      <p>• React</p>
                    </div>
                  </TabPanel>
                  <TabPanel value={1}>
                    <div className="panel2">
                      <p>• VS code</p>
                      <p>• Visual studio</p>
                      <p>• Figma</p>
                      <p>• GitHub</p>
                      <p>• SQL studio</p>
                      <p>• Virtual Machines</p>
                      <p>• Command Line Interfaces</p>
                      <p>• Arduinos and Arduino IDE</p>
                    </div>
                  </TabPanel>
                  <TabPanel value={2}>
                    <div className="panel3">
                      <p>Proper documentation and record keeping</p>
                      <p>Algorithm design</p>
                      <p>Problem solving</p>
                      <p>Works great in team setting</p>
                      <p>Excellent Communicator</p>
                    </div>
                  </TabPanel>
                  <TabPanel value={3}>
                    <div className="panel4">
                      <p>• BSc. in Computer Science from Macewan University</p>
                      <p>• BSc. in Chemistry from Concordia University of Edmonton</p>
                    </div>
                  </TabPanel>
                </Tabs>
              </div>
            </div>
            <div className="S6right">
              <div className="ContactContainer">
                <div className="ContactMe">Contact Me</div>
                <div className="ContactBody">Feel free to contact me on any of my socials below or by email at:</div>
                <div className="ContactEmail">adam_polywkan@live.ca</div>
                <div className="ContactButtons">
                  <a href="https://github.com/Apolywkan">
                    <img class="GithubMark" src={githubMark} alt="githubMark"></img>
                  </a>
                  <a href="https://www.linkedin.com/in/adam-polywkan-b383bb212/">
                    <img class="LinkedinMark" src={linkedinMark} alt="linkedinMark"></img>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="S6bottom">
          <div class="Wave"></div>
          <div class="Wave"></div>
          <div class="Wave"></div>
        </div>
      </div>
    </div>
  );
}

export default App;
